  mounted() {
    window.scrollTo(0, 0);
    window.addEventListener("resize", this.onResize);
    Vue.nextTick(() => {
      var margin = { top: 10, right: 30, bottom: 40, left: 50 },
        width = 300 - margin.left - margin.right,
        height = 80 - margin.top - margin.bottom;
      const svg = d3
        .select("#CountOf")
        .append("svg")
        .attr("preserveAspectRatio", "xMinYMin meet")
        .attr("viewBox", "0 0 300 25")
        .classed("svg-content", true)
        .attr("width", width)
        .attr("height", height);
      // Add X axis
      var x = d3
        .scaleBand()
        .domain(this.userFilter.map((d) => d.imgname))
        .range([0, width]);
      // Add dots
      svg
        .append("g")
        .selectAll("dot")
        .data(this.userFilter)
        .enter()
        .append("circle")

        .attr("cx", function (d) {
          return x(d.imgname) + 10;
        })
        .attr("cy", function () {
          return 10;
        })
        .attr("r", 3.5);
      let g = this.indexNumber + 1;
      svg.selectAll("circle").attr("fill", "black").attr("r", 2.5);

      svg
        .select("circle:nth-child(" + g + ")")
        .attr("fill", "red")
        .attr("r", 5.5);
    });
  },
  methods: {
    onResize() {
      this.windowWidth = window.innerWidth;
    },
    colorUpdate() {
      const svg = d3.select("#CountOf");
      let g = this.indexNumber + 1;
      svg.selectAll("circle").attr("fill", "black").attr("r", 2.5);

      svg
        .select("circle:nth-child(" + g + ")")
        .attr("fill", "red")
        .attr("r", 5.5);
    },
    selectIt(g) {
      let value = g;
      this.indexNumber = value;
    },
    countIt() {
      if (this.indexNumber < this.userFilter.length - 1) {
        return this.indexNumber++;
      } else return (this.indexNumber = 0);
    },
    countItDown() {
      if (this.indexNumber > 0) {
        return this.indexNumber--;
      }
    },
  },
  computed: {
    userFilter() {
      return this[this.uFilterKey];
    },
    setAbrev() {
      let g;
      if (this.selectionL == "ToThePeopleOfTheWorld") {
        g = "TTPW";
      }
      if (this.selectionL == "Monochromes") {
