      }
      if (this.selectionL == "CrosbyStreetStudio") {
        g = "Crosby Street Studio, 1979-1987";
      }
      if (this.selectionL == "EarlyWork") {
        g = "Early Work";
      }
      if (this.selectionL == "Prints") {
        g = "Prints";
      }
      if (this.selectionL == "CrosbyStreetStudio2") {
        g = "Crosby Street , 1988-2014";
      }
      if (this.selectionL == "ElizabethStreet") {
        g = "Elizabeth St , 1978-79";
      }
      if (this.selectionL == "RecentWork") {
        g = "Recent Work";
      }
      if (this.selectionL == "WorksOnPaper") {
        g = "Drawings";
      }
      if (this.selectionL == "Sketchbooks") {
        g = "Sketchbooks";
      }
      return g;
    },
    filt: function () {
      return this.inventory.filter(function (el) {
        return el.series === this.setAbrev;
      }, this);
    },
    all() {
      return this.inventory;
    },
    ToThePeopleOfTheWorld() {
      let f = this.inventory.filter((item) => item.series === this.setAbrev);
      var result = this.ttpw.map((x) => f.find((p) => p.id === x));
      return result;
    },

    Monochromes() {
      let f = this.inventory.filter((f) =>
        this.monochromes.some((item) => item === f.id)
      );
      var result = this.monochromes.map((x) => f.find((p) => p.id === x));
      return result;
    },
    RecentWork() {
      let f = this.inventory.filter((f) =>
        this.recentWork.some((item) => item === f.id)
      );
      var result = this.recentWork.map((x) => f.find((p) => p.id === x));
      return result;
    },
    CrosbyStreetStudio() {
      let f = this.inventory.filter((f) =>
        this.crosbyStreetStudio.some((item) => item === f.id)
      );
      var result = this.crosbyStreetStudio.map((x) =>
        f.find((p) => p.id === x)
      );
      return result;
    },
    CrosbyStreetStudio2() {
