<script>
import * as d3 from "d3";
// import Footer from "@/components/Footer.vue";
import image from "@/assets/full-size/DA163.jpg";
import portrait from "@/assets/full-size/donaldAlbertiPortrait.jpg";
import studio from "@/assets/full-size/AlbertiStudioTTPW.jpg";
import SubmenuLinks from "@/components/SubmenuLinks.vue";
var fadeInElements = [];
var offsetStart = 0;
var offsetEnd = 0;
export default {
  name: "Bio-o",
  data() {
    return {
      image,
      portrait,
      studio,
      timelineData: [],
      imgArray: [
        "IMG_1919",
        "first-band",
        "DA069",
        "DA067",
        "DA057",
        "DA059",
        "DA016",
        "AlbertiStudioTTPW",
      ],
    };
  },
  mounted() {
    window.addEventListener(
      "scroll",
      () => {
        document.documentElement.style.setProperty(
          "--scroll",
          -(window.pageYOffset - offsetStart) /
            (offsetStart - offsetEnd - window.innerHeight) /
            2
        );
      },
      false
    );
    window.scrollTo(0, 0);

    Promise.all([d3.csv("/DWATimeline.csv")]).then((data) => {
      this.timelineData = data[0];
      window.addEventListener("scroll", this.handleScroll);
      this.handleScroll;
    });
  },
  Unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  computed: {
    arrval() {
      fadeInElements = Array.from(document.getElementsByClassName("fade-in"));
      return fadeInElements;
    },
  },
  methods: {
    handleScroll() {
      // console.log("scrolling");
      for (var i = 0; i < this.arrval.length; i++) {
        var elem = this.arrval[i];
        if (this.isElemVisible(elem)) {
          elem.style.opacity = "1";

          elem.style.transform = "scale(1)";
          this.arrval.splice(i, 1); // only allow it to run once
        }
      }
    },
    handleScroll2() {
      // console.log("scrolling");
      for (var i = 0; i < this.arrval.length; i++) {
        var elem = this.arrval[i];
        if (this.isElemVisible(elem)) {
          elem.style.opacity = "1";
          elem.style.transform = "scale(1)";
          this.arrval.splice(i, 5); // only allow it to run once
        }
      }
    },
    isElemVisible(el) {
      var rect = el.getBoundingClientRect();
      var elemTop = rect.top + 300; // 200 = buffer
      var elemBottom = rect.bottom;
      return elemTop < window.innerHeight && elemBottom >= 0;
    },
  },
  components: { SubmenuLinks },
  props: {
    inventory: Array,
    listOfSupports: Array,
    listOfMediums: Array,
    listOfSeries: Array,
  },
};
</script>
